@import url("https://fonts.googleapis.com/css2?family=Goblin+One&display=swap");

.header {
  padding: 10px 10px 30px 10px;
  height: 600px;
  max-height: 560px;

  @media (max-width: 1000px) {
    padding: 0 0 30px;
  }
  .header__body {
    display: flex;
    flex-direction: column;
    .header__top {
      height: 57px;
      width: 100%;
      background-color: #90480a;

      padding: 5px 0;

      display: flex;
      align-items: center;
      justify-content: space-between;

      .header__brand {
        a {
          display: flex;
          align-items: center;
          justify-content: center;

          .header__brand-text {
            color: white;
            padding: 0;
            font-size: 36px;
          }

          .header__book-icon {
            margin: 0 10px;
            width: 45px;
          }
        }
      }

      .header__navbar {
        display: flex;
        align-items: center;
        justify-content: space-around;
        width: 600px;

        .header__nav-item {
          color: white;
          font-weight: 600;
        }
      }

      .header__shopping {
        padding: 5px 10px;
        background-color: #1e345f;
        width: 134px;
        height: 36px;

        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 10px;

        .header__shopping-link {
          color: white;
          align-items: center;
          justify-content: center;
          display: flex;
          font-weight: 600;
          font-size: 16px;

          .header__shopping-icon {
            width: 30px;
            margin-right: 5px;
          }
        }
      }
    }

    .header__main {
      margin: 15px 0;
      height: 501px;
      width: 100%;
      background-image: url("../../assets/images/header-main-bg.png");
      background-repeat: no-repeat;
      background-size: cover;
      position: relative;

      .header__sentence {
        max-width: 604px;
        width: 90%;
        height: 31px;

        position: absolute;
        top: 62px;
        left: 30vw;

        @media (max-width: 1000px) {
          left: 20px;

          img {
            width: 70vw;
          }
        }

      }

      .header__info {
        width: 90%;
        max-width: 660px;
        height: 180px;
        position: absolute;
        left: 2vw;
        bottom: 20px;

        .header__hr {
          background: #fffdfd;
          width: 90%;
          height: 1px;
          margin: 19px 5px;

          @media (max-width: 700px) {
            width: 60%;
          }
          @media (max-width: 500px) {
            width: 90%;


          }
        }

        .header__text {
          position: relative;

          .header__description {
            z-index: 500;
            position: absolute;
            font-weight: 400;
            font-size: 22px;
            line-height: 25px;
            margin: 0;
            color: white;
            font-family: "Goblin One", serif;

            @media (max-width: 1000px) {
              font-size: 2vw;
              line-height: normal;
            }


            @media (max-width: 500px) {
              font-size: 3vw;
              line-height: normal;
            }
          }

          .header__blur {
            position: absolute;
            top: -11px;
            left: -20px;
            width: 90%;
            min-height: 177px;
            background: #2e4c5f;

            filter: blur(22.5px);
            z-index: 400;
            @media (max-width: 700px) {
              top: -29px;

              min-height: 150px;
              width: 60%;
            }

            @media (max-width: 500px) {
              font-size: 3vw;
              line-height: normal;
              width: 100%;

            }
          }
        }
      }
    }
  }
}

.header__burger-menu {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.492);

  .header__burger-body {
    position: relative;
    width: 300px;
    height: 100%;
    background-color: #90480a;
    padding: 0 10px;
    .header__brand {
      a {
        display: flex;
        align-items: center;

        .header__brand-text {
          color: white;
          padding: 0;
          font-size: 36px;
        }

        .header__book-icon {
          width: 45px;
        }
      }
    }

    .header__navbar {
      display: flex !important;
      flex-direction: column;
      padding: 0;

      .header__nav-item {
        color: white;
        font-weight: 600;
      }
    }

    .header__shopping {
      padding: 5px 10px;
      background-color: #1e345f;
      width: 134px;
      height: 36px;

      display: flex;
      align-items: center;
      justify-content: center;
      margin: 50px 0;

      .header__shopping-link {
        color: white;
        align-items: center;
        justify-content: center;
        display: flex;
        font-weight: 600;
        font-size: 16px;

        .header__shopping-icon {
          width: 30px;
          margin-right: 5px;
        }
      }
    }
  }
}

.header__burger-menu-icon {
  cursor: pointer;
  font-size: 24px;
  color: white;
  margin-right: 10px;
  display: none;
}

.header__burger-close{
  display: none;
  position: absolute;
  top: 35px;
  right: 25px;
  color: white;
}

.header__burger-menu.active {
  display: flex;
  justify-content: right;
}

@media (max-width: 1000px) {
  .header__top {
    .header__burger-menu-icon {
      display: initial;
    }

    .header__navbar {
      display: none !important;
    }

    .header__shopping {
      display: none !important;
    }
  }
}


@media (max-width: 500px){
  .header__burger-close{
    display: flex;
  }
}
